.rooms_modal {
  text-align: center;
  background: -webkit-gradient(linear, left top, right top, from(#FC466B), to(#3F5EFB));
  background: -webkit-linear-gradient(left, #FC466B 0%, #3F5EFB 100%);
  background: -o-linear-gradient(left, #FC466B 0%, #3F5EFB 100%);
  background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%); }
  .rooms_modal h3 {
    color: black;
    font-size: 2em; }
  .rooms_modal h4 {
    background: green;
    width: 60%;
    margin: 0 auto;
    padding: 10px 0;
    color: white;
    background: -webkit-gradient(linear, left top, right top, from(#FC466B), to(#3F5EFB));
    background: -webkit-linear-gradient(left, #FC466B 0%, #3F5EFB 100%);
    background: -o-linear-gradient(left, #FC466B 0%, #3F5EFB 100%);
    background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%);
    border-radius: 20px; }
  .rooms_modal .link_to_timetable {
    text-decoration: none; }

.rooms_container {
  position: relative; }
  .rooms_container img {
    width: 30px;
    position: absolute;
    right: 30px; }
  .rooms_container h3 {
    cursor: -webkit-zoom-in;
    cursor: zoom-in; }

@media only screen and (max-width: 920px) {
  .rooms_container img {
    display: none; } }



